<script>
export default {
  methods: {
    closeOverlay () {
      let offsetArticles = $('.article_list').offset().top
      if ($(window).width() > 880) {
        $(window).scrollTop(offsetArticles - 40)
      } else if ($(window).width() <= 880) {
        $(window).scrollTop(offsetArticles)
      }
      $('.menu').removeClass('menu_open')
      $('.hamburger').removeClass('cross')
      setTimeout(() => {
        $('.home_page').css('opacity', 1)
      }, 500)
    }
  }
}
</script>

